import { useContext, useState } from 'react';
import {
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
  TextField,
} from '@mui/material';
import { AdapterDateFns } from '@mui/x-date-pickers/AdapterDateFns';
import { LocalizationProvider } from '@mui/x-date-pickers/LocalizationProvider';
import { DatePicker } from '@mui/x-date-pickers/DatePicker';
import { INearProps, NearContext } from '../../services/near';
import { addYear } from '../../utils';
import { TMetadata } from '../../services/tokenApi';

export default function CreateLockup({ token } : { token: TMetadata }) {
  const {
    near,
  }: {
    near: INearProps | null,
  } = useContext(NearContext);

  const [open, setOpen] = useState(false);
  const [startDate, setStartDate] = useState<Date | null>(null);
  const handleOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);

  const handleCreateLockup = async (e: any) => {
    if (!near) {
      throw Error('Cannot access token api');
    }

    e.preventDefault();

    const { account, amount } = e.target.elements;

    console.log(account.value, amount.value);

    const lockupContractId = near?.api.getContract().contractId || '';
    const claimedBalance = '0';
    const userAccountId = account.value;
    const lockupTotalAmount = amount.value * 10 ** token.decimals;

    const ts = (startDate?.getTime() || 0) / 1000;

    const schedule = [
      { timestamp: ts, balance: '0' },
      { timestamp: addYear(startDate, 1) - 1, balance: '0' },
      { timestamp: addYear(startDate, 1), balance: (lockupTotalAmount / 4).toString() },
      { timestamp: addYear(startDate, 4), balance: lockupTotalAmount.toString() },
    ];

    near.tokenApi.ftTransferCall({
      receiver_id: lockupContractId,
      amount: lockupTotalAmount.toString(),
      msg: {
        account_id: userAccountId,
        schedule,
        claimed_balance: claimedBalance,
      },
    });
  };

  return (
    <div>
      <button className="button" type="button" onClick={handleOpen}>Create Lockup</button>
      <Dialog open={open} maxWidth="xs" onClose={handleClose}>
        <form className="form_offer" onSubmit={handleCreateLockup}>
          <DialogTitle>Create Lockup</DialogTitle>
          <DialogContent>
            <TextField
              margin="normal"
              id="account"
              label="Account Id"
              type="text"
              sx={{ width: '75%' }}
              variant="standard"
            />
            <TextField
              margin="normal"
              id="amount"
              label="Amount"
              type="text"
              variant="standard"
              sx={{ width: '50%' }}
            />
            <br />
            <LocalizationProvider dateAdapter={AdapterDateFns}>
              <DatePicker
                label="Start date"
                value={startDate}
                onChange={(newValue) => {
                  setStartDate(newValue);
                }}
                renderInput={
                  (params) => (
                    <TextField
                      margin="normal"
                      sx={{ width: '50%' }}
                      variant="standard"
                      {...params}
                    />
                  )
                }
              />
            </LocalizationProvider>
          </DialogContent>
          <DialogActions>
            <Button onClick={handleClose}>Cancel</Button>
            <Button type="submit">Create</Button>
          </DialogActions>
        </form>
      </Dialog>
    </div>
  );
}
